"use client"

import { useState } from "react"
import {
  RefreshCw,
  Plus,
} from "lucide-react"
import { cn } from "@/lib/utils"

// Mock categories data - 中文化
const categoriesData = [
  { id: "hr", name: "人事服务", code: "hr-services", count: 3 },
  { id: "office", name: "办公支持", code: "office-support", count: 2 },
  { id: "assets", name: "设备与资产", code: "equipment-assets", count: 4 },
  { id: "access", name: "账号与权限管理", code: "account-access", count: 5 },
]

interface CategorySidebarProps {
  selectedCategory: string
  onSelectCategory: (id: string) => void
  onAddCategory: () => void
}

export function CategorySidebar({ selectedCategory, onSelectCategory, onAddCategory }: CategorySidebarProps) {
  return (
    <aside className="w-full h-full bg-white border-r border-gray-100 flex flex-col">
      {/* Header */}
      <div className="px-5 py-4 border-b border-gray-100">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-base font-semibold text-foreground">服务目录</h2>
            <p className="text-xs text-muted-foreground mt-0.5">{categoriesData.length} 个目录</p>
          </div>
          <div className="flex items-center gap-1">
            <button
              type="button"
              className="w-8 h-8 rounded-lg flex items-center justify-center text-gray-400 hover:text-gray-600 hover:bg-gray-100 transition-all"
            >
              <RefreshCw className="w-4 h-4" />
            </button>
            <button
              type="button"
              onClick={onAddCategory}
              className="w-8 h-8 rounded-lg flex items-center justify-center bg-gray-100 text-gray-600 hover:bg-indigo-100 hover:text-indigo-600 transition-all"
            >
              <Plus className="w-4 h-4" />
            </button>
          </div>
        </div>
      </div>

      {/* Category List */}
      <nav className="flex-1 p-3 overflow-y-auto">
        <div className="space-y-1">
          {categoriesData.map((category) => {
            const isActive = selectedCategory === category.id
            return (
              <button
                key={category.id}
                type="button"
                onClick={() => onSelectCategory(category.id)}
                className={cn(
                  "w-full flex flex-col items-start px-4 py-3 rounded-xl text-left transition-all duration-200",
                  isActive
                    ? "bg-gray-100 border border-gray-200"
                    : "hover:bg-gray-50"
                )}
              >
                <span className={cn(
                  "text-sm font-medium",
                  isActive ? "text-foreground" : "text-gray-700"
                )}>
                  {category.name}
                </span>
                <span className="text-xs text-muted-foreground mt-0.5">
                  {category.code}
                </span>
              </button>
            )
          })}
        </div>
      </nav>
    </aside>
  )
}
