"use client"

import React from "react"
import { cn } from "@/lib/utils"

interface Service {
  id: string
  title: string
  code: string
  description: string
  icon: React.ElementType
  status: "active" | "draft"
  sla: string
  category: string
  categoryName?: string
}

interface ServiceCardProps {
  service: Service
  onEdit?: () => void
}

export function ServiceCard({ service, onEdit }: ServiceCardProps) {
  const Icon = service.icon

  return (
    <div
      onClick={() => onEdit?.()}
      className="group relative bg-white border border-gray-100 rounded-2xl shadow-sm hover:shadow-md hover:border-indigo-100 transition-all duration-300 overflow-hidden cursor-pointer"
    >
      <div className="p-5">
        {/* Header */}
        <div className="flex items-start gap-4">
          <div className="w-11 h-11 rounded-xl bg-gray-50 border border-gray-100 flex items-center justify-center text-gray-400 flex-shrink-0">
            <Icon className="w-5 h-5" />
          </div>
          <div className="flex-1 min-w-0">
            <div className="flex items-start justify-between gap-2">
              <h3 className="text-sm font-semibold text-foreground group-hover:text-primary transition-colors">
                {service.title}
              </h3>
              <span
                className={cn(
                  "flex items-center gap-1.5 px-2 py-0.5 rounded text-xs font-medium flex-shrink-0",
                  service.status === "active"
                    ? "text-emerald-600"
                    : "text-gray-500"
                )}
              >
                <span className={cn(
                  "w-1.5 h-1.5 rounded-full",
                  service.status === "active" ? "bg-emerald-500" : "bg-gray-400"
                )} />
                启用
              </span>
            </div>
            <p className="text-xs text-muted-foreground mt-0.5 font-mono">
              {service.code}
            </p>
          </div>
        </div>

        {/* Description */}
        <p className="text-sm text-muted-foreground mt-3 line-clamp-2">
          {service.description}
        </p>

        {/* Footer */}
        <div className="flex items-center gap-4 mt-4 pt-3 border-t border-gray-50">
          <span className="text-xs text-muted-foreground">
            {service.categoryName || "人事服务"}
          </span>
          <span className="text-xs text-muted-foreground">·</span>
          <span className="text-xs text-muted-foreground">
            SLA {service.sla}
          </span>
        </div>
      </div>
    </div>
  )
}
