"use client"

import { useState, useEffect } from "react"
import {
  X,
  Wand2,
  Bold,
  Italic,
  Link,
  List,
  ChevronDown,
  Sparkles,
  Clock,
  Plus,
  FileText,
  Trash2,
  Upload,
  Cloud,
  Paperclip,
} from "lucide-react"
import {
  Sheet,
  SheetContent,
} from "@/components/ui/sheet"
import { cn } from "@/lib/utils"

interface CreateServiceDrawerProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  editingService?: {
    id: string
    title: string
    code: string
    description: string
    category: string
    sla: string
  } | null
}

// Mock categories for dropdown
const categories = [
  { id: "hr", label: "人事服务" },
  { id: "office", label: "办公支持" },
  { id: "assets", label: "设备与资产" },
  { id: "access", label: "账号与权限管理" },
]

// Mock knowledge items
const mockKnowledgeItems = [
  { id: "1", title: "密码重置操作指南", type: "text", enabled: true },
  { id: "2", title: "VPN连接故障排查手册.pdf", type: "file", enabled: true },
  { id: "3", title: "常见问题FAQ", type: "text", enabled: false },
]

// Rich Text Editor Toolbar Component
function RichTextToolbar() {
  return (
    <div className="flex items-center gap-1 px-3 py-2 border-b border-gray-200 bg-white rounded-t-xl">
      <button
        type="button"
        className="p-1.5 rounded-md text-gray-500 hover:text-gray-700 hover:bg-gray-100 transition-all"
      >
        <Bold className="w-4 h-4" />
      </button>
      <button
        type="button"
        className="p-1.5 rounded-md text-gray-500 hover:text-gray-700 hover:bg-gray-100 transition-all"
      >
        <Italic className="w-4 h-4" />
      </button>
      <button
        type="button"
        className="p-1.5 rounded-md text-gray-500 hover:text-gray-700 hover:bg-gray-100 transition-all"
      >
        <Link className="w-4 h-4" />
      </button>
      <button
        type="button"
        className="p-1.5 rounded-md text-gray-500 hover:text-gray-700 hover:bg-gray-100 transition-all"
      >
        <List className="w-4 h-4" />
      </button>
    </div>
  )
}

export function CreateServiceDrawer({ open, onOpenChange, editingService }: CreateServiceDrawerProps) {
  const [activeTab, setActiveTab] = useState<"basic" | "knowledge">("basic")
  const [formData, setFormData] = useState({
    categoryId: "",
    name: "",
    code: "",
    icon: "",
    sla: "",
    description: "",
    collaborationRules: "",
    infoCollectionPrompt: "",
    enabled: true,
  })
  const [categoryDropdownOpen, setCategoryDropdownOpen] = useState(false)
  const [knowledgeItems, setKnowledgeItems] = useState(mockKnowledgeItems)
  const [showAddKnowledge, setShowAddKnowledge] = useState(false)
  const [newKnowledge, setNewKnowledge] = useState({
    title: "",
    type: "text" as "text" | "file",
    content: "",
  })

  // 当编辑服务时，填充表单数据
  useEffect(() => {
    if (editingService) {
      setFormData({
        ...formData,
        categoryId: editingService.category,
        name: editingService.title,
        code: editingService.code,
        description: editingService.description,
        sla: editingService.sla.replace('h', ''),
      })
    } else {
      setFormData({
        categoryId: "",
        name: "",
        code: "",
        icon: "",
        sla: "",
        description: "",
        collaborationRules: "",
        infoCollectionPrompt: "",
        enabled: true,
      })
    }
  }, [editingService, open])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    console.log("Save service:", formData, knowledgeItems)
    onOpenChange(false)
  }

  const handleReset = () => {
    setFormData({
      categoryId: "",
      name: "",
      code: "",
      icon: "",
      sla: "",
      description: "",
      collaborationRules: "",
      infoCollectionPrompt: "",
      enabled: true,
    })
  }

  const handleAddKnowledge = () => {
    if (newKnowledge.title) {
      setKnowledgeItems([
        ...knowledgeItems,
        {
          id: Date.now().toString(),
          title: newKnowledge.title,
          type: newKnowledge.type,
          enabled: true,
        },
      ])
      setNewKnowledge({ title: "", type: "text", content: "" })
      setShowAddKnowledge(false)
    }
  }

  const handleDeleteKnowledge = (id: string) => {
    setKnowledgeItems(knowledgeItems.filter((item) => item.id !== id))
  }

  const toggleKnowledgeEnabled = (id: string) => {
    setKnowledgeItems(
      knowledgeItems.map((item) =>
        item.id === id ? { ...item, enabled: !item.enabled } : item
      )
    )
  }

  const selectedCategory = categories.find((c) => c.id === formData.categoryId)

  return (
    <Sheet open={open} onOpenChange={onOpenChange}>
      <SheetContent
        side="right"
        className="w-full sm:max-w-none sm:w-[65vw] p-0 bg-white shadow-2xl border-l border-gray-200 flex flex-col"
      >
        {/* Header */}
        <div className="flex items-center justify-between px-8 py-5 border-b border-gray-100 bg-white">
          <div className="flex items-center gap-4">
            <h2 className="text-lg font-semibold text-gray-900">
              {editingService ? "编辑服务" : "添加服务策略"}
            </h2>
            <button
              type="button"
              className="flex items-center gap-2 px-3 py-1.5 rounded-lg text-sm font-medium text-indigo-600 hover:bg-indigo-50 transition-all"
            >
              <Wand2 className="w-4 h-4" />
              AI 自动生成
            </button>
          </div>
          <button
            type="button"
            onClick={() => onOpenChange(false)}
            className="w-8 h-8 rounded-lg flex items-center justify-center text-gray-400 hover:text-gray-600 hover:bg-gray-100 transition-all"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        {/* Tabs */}
        <div className="px-8 py-3 border-b border-gray-100 bg-gray-50/50">
          <div className="flex gap-1">
            <button
              type="button"
              onClick={() => setActiveTab("basic")}
              className={cn(
                "px-4 py-2 rounded-lg text-sm font-medium transition-all",
                activeTab === "basic"
                  ? "bg-white text-foreground shadow-sm"
                  : "text-muted-foreground hover:text-foreground"
              )}
            >
              基本信息
            </button>
            <button
              type="button"
              onClick={() => setActiveTab("knowledge")}
              className={cn(
                "px-4 py-2 rounded-lg text-sm font-medium transition-all",
                activeTab === "knowledge"
                  ? "bg-white text-foreground shadow-sm"
                  : "text-muted-foreground hover:text-foreground"
              )}
            >
              知识库配置
            </button>
          </div>
        </div>

        {/* Scrollable Body */}
        <div className="flex-1 overflow-y-auto">
          {activeTab === "basic" ? (
            <div className="px-8 py-6 space-y-8">
              {/* Section A: 基础信息 */}
              <section className="space-y-5">
                <div className="flex items-center gap-2">
                  <div className="w-1.5 h-5 rounded-full bg-indigo-500" />
                  <h3 className="text-base font-semibold text-gray-900">基础信息</h3>
                </div>

                {/* 所属目录 */}
                <div className="space-y-2">
                  <label className="text-sm font-medium text-gray-700">
                    所属目录 <span className="text-red-500">*</span>
                  </label>
                  <div className="relative">
                    <button
                      type="button"
                      onClick={() => setCategoryDropdownOpen(!categoryDropdownOpen)}
                      className="w-full flex items-center justify-between px-4 py-2.5 rounded-xl border border-gray-200 text-sm bg-white hover:border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                    >
                      <span className={selectedCategory ? "text-gray-900" : "text-gray-400"}>
                        {selectedCategory?.label || "请选择服务目录"}
                      </span>
                      <ChevronDown className={cn(
                        "w-4 h-4 text-gray-400 transition-transform",
                        categoryDropdownOpen && "rotate-180"
                      )} />
                    </button>
                    {categoryDropdownOpen && (
                      <div className="absolute z-10 top-full mt-1 w-full bg-white border border-gray-200 rounded-xl shadow-lg overflow-hidden">
                        {categories.map((category) => (
                          <button
                            key={category.id}
                            type="button"
                            onClick={() => {
                              setFormData({ ...formData, categoryId: category.id })
                              setCategoryDropdownOpen(false)
                            }}
                            className={cn(
                              "w-full px-4 py-2.5 text-sm text-left hover:bg-indigo-50 transition-all",
                              formData.categoryId === category.id
                                ? "bg-indigo-50 text-indigo-600"
                                : "text-gray-700"
                            )}
                          >
                            {category.label}
                          </button>
                        ))}
                      </div>
                    )}
                  </div>
                </div>

                {/* 服务名称 & 编码 - Grid Layout */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <label className="text-sm font-medium text-gray-700">
                      服务名称 <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      required
                      placeholder="例如：密码重置服务"
                      value={formData.name}
                      onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                      className="w-full px-4 py-2.5 rounded-xl border border-gray-200 text-sm placeholder:text-gray-400 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                    />
                  </div>
                  <div className="space-y-2">
                    <label className="text-sm font-medium text-gray-700">
                      服务编码 <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      required
                      placeholder="唯一标识，如 srv_pwd_reset"
                      value={formData.code}
                      onChange={(e) => setFormData({ ...formData, code: e.target.value })}
                      className="w-full px-4 py-2.5 rounded-xl border border-gray-200 text-sm font-mono placeholder:text-gray-400 placeholder:font-sans focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                    />
                  </div>
                </div>

                {/* 图标 & SLA - Grid Layout */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <label className="text-sm font-medium text-gray-700">
                      图标
                    </label>
                    <input
                      type="text"
                      placeholder="图标类名或 URL"
                      value={formData.icon}
                      onChange={(e) => setFormData({ ...formData, icon: e.target.value })}
                      className="w-full px-4 py-2.5 rounded-xl border border-gray-200 text-sm placeholder:text-gray-400 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                    />
                  </div>
                  <div className="space-y-2">
                    <label className="text-sm font-medium text-gray-700">
                      SLA 时间
                    </label>
                    <div className="relative">
                      <input
                        type="number"
                        placeholder="响应时限"
                        value={formData.sla}
                        onChange={(e) => setFormData({ ...formData, sla: e.target.value })}
                        className="w-full px-4 py-2.5 pr-14 rounded-xl border border-gray-200 text-sm placeholder:text-gray-400 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                      />
                      <span className="absolute right-4 top-1/2 -translate-y-1/2 text-sm text-gray-400">
                        小时
                      </span>
                    </div>
                  </div>
                </div>

                {/* 描述 */}
                <div className="space-y-2">
                  <label className="text-sm font-medium text-gray-700">
                    服务描述
                  </label>
                  <textarea
                    rows={3}
                    placeholder="请输入服务描述..."
                    value={formData.description}
                    onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                    className="w-full px-4 py-2.5 rounded-xl border border-gray-200 text-sm placeholder:text-gray-400 resize-none focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                  />
                </div>
              </section>

              {/* Section B: Agent 配置 */}
              <section className="space-y-5">
                <div className="flex items-center gap-2">
                  <div className="w-1.5 h-5 rounded-full bg-violet-500" />
                  <h3 className="text-base font-semibold text-gray-900">Agent 配置</h3>
                  <span className="text-xs text-gray-400 ml-1">The Brain</span>
                </div>

                {/* 协作规范 - Rich Text */}
                <div className="space-y-2">
                  <label className="text-sm font-medium text-gray-700">
                    协作规范
                  </label>
                  <div className="rounded-xl border border-gray-200 overflow-hidden bg-gray-50/50">
                    <RichTextToolbar />
                    <textarea
                      rows={4}
                      placeholder="例如：需要 IT 管理员审批..."
                      value={formData.collaborationRules}
                      onChange={(e) => setFormData({ ...formData, collaborationRules: e.target.value })}
                      className="w-full px-4 py-3 text-sm placeholder:text-gray-400 resize-none focus:outline-none bg-gray-50/50 border-0"
                    />
                  </div>
                </div>

                {/* 信息采集提示 - Rich Text with Label */}
                <div className="space-y-2">
                  <div className="flex items-center justify-between">
                    <label className="text-sm font-medium text-gray-700">
                      信息采集提示
                    </label>
                    <span className="flex items-center gap-1 px-2 py-0.5 rounded-md bg-indigo-100 text-xs font-medium text-indigo-600">
                      <Sparkles className="w-3 h-3" />
                      AI Reasoning Context
                    </span>
                  </div>
                  <div className="rounded-xl border border-gray-200 overflow-hidden bg-gray-50/50">
                    <RichTextToolbar />
                    <textarea
                      rows={6}
                      placeholder="定义 AI 在处理服务请求时需要收集的信息和推理上下文..."
                      value={formData.infoCollectionPrompt}
                      onChange={(e) => setFormData({ ...formData, infoCollectionPrompt: e.target.value })}
                      className="w-full px-4 py-3 text-sm placeholder:text-gray-400 resize-none focus:outline-none bg-gray-50/50 border-0"
                    />
                  </div>
                </div>
              </section>

              {/* Section C: 高级设置 */}
              <section className="space-y-5">
                <div className="flex items-center gap-2">
                  <div className="w-1.5 h-5 rounded-full bg-gray-400" />
                  <h3 className="text-base font-semibold text-gray-900">高级设置</h3>
                </div>

                {/* 是否启用 - Toggle Switch */}
                <div className="flex items-center justify-between py-2 px-4 rounded-xl bg-gray-50/80 border border-gray-100">
                  <div>
                    <label className="text-sm font-medium text-gray-700">
                      是否启用
                    </label>
                    <p className="text-xs text-gray-400 mt-0.5">启用后该服务将在服务目录中显示</p>
                  </div>
                  <button
                    type="button"
                    role="switch"
                    aria-checked={formData.enabled}
                    onClick={() => setFormData({ ...formData, enabled: !formData.enabled })}
                    className={cn(
                      "relative w-11 h-6 rounded-full transition-colors duration-200 ease-in-out focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2",
                      formData.enabled ? "bg-indigo-600" : "bg-gray-200"
                    )}
                  >
                    <span
                      className={cn(
                        "absolute top-0.5 left-0.5 w-5 h-5 bg-white rounded-full shadow-md transition-transform duration-200 ease-in-out",
                        formData.enabled ? "translate-x-5" : "translate-x-0"
                      )}
                    />
                  </button>
                </div>
              </section>
            </div>
          ) : (
            /* Knowledge Tab */
            <div className="px-8 py-6 space-y-6">
              <div className="flex items-center justify-between">
                <div>
                  <h3 className="text-base font-semibold text-gray-900">知识库配置</h3>
                  <p className="text-sm text-muted-foreground mt-0.5">
                    为该服务配置专属知识库，AI 将基于这些知识进行推理
                  </p>
                </div>
                <button
                  type="button"
                  onClick={() => setShowAddKnowledge(true)}
                  className="flex items-center gap-2 px-4 py-2 rounded-lg bg-indigo-600 text-white text-sm font-medium hover:bg-indigo-700 transition-all"
                >
                  <Plus className="w-4 h-4" />
                  添加知识
                </button>
              </div>

              {/* Add Knowledge Form */}
              {showAddKnowledge && (
                <div className="p-5 rounded-xl border border-indigo-200 bg-indigo-50/30 space-y-4">
                  <div className="space-y-2">
                    <label className="text-sm font-medium text-gray-700">知识标题</label>
                    <input
                      type="text"
                      placeholder="请输入知识标题"
                      value={newKnowledge.title}
                      onChange={(e) => setNewKnowledge({ ...newKnowledge, title: e.target.value })}
                      className="w-full px-4 py-2.5 rounded-xl border border-gray-200 text-sm placeholder:text-gray-400 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all bg-white"
                    />
                  </div>
                  
                  <div className="space-y-2">
                    <label className="text-sm font-medium text-gray-700">知识类型</label>
                    <div className="flex p-1 bg-gray-100 rounded-xl w-fit">
                      <button
                        type="button"
                        onClick={() => setNewKnowledge({ ...newKnowledge, type: "text" })}
                        className={cn(
                          "flex items-center gap-2 px-4 py-2 rounded-lg text-sm font-medium transition-all",
                          newKnowledge.type === "text"
                            ? "bg-white text-indigo-600 shadow-sm"
                            : "text-gray-500 hover:text-gray-700"
                        )}
                      >
                        <FileText className="w-4 h-4" />
                        文本
                      </button>
                      <button
                        type="button"
                        onClick={() => setNewKnowledge({ ...newKnowledge, type: "file" })}
                        className={cn(
                          "flex items-center gap-2 px-4 py-2 rounded-lg text-sm font-medium transition-all",
                          newKnowledge.type === "file"
                            ? "bg-white text-indigo-600 shadow-sm"
                            : "text-gray-500 hover:text-gray-700"
                        )}
                      >
                        <Paperclip className="w-4 h-4" />
                        文件
                      </button>
                    </div>
                  </div>

                  {newKnowledge.type === "text" ? (
                    <div className="space-y-2">
                      <label className="text-sm font-medium text-gray-700">知识内容</label>
                      <textarea
                        rows={4}
                        placeholder="请输入知识内容..."
                        value={newKnowledge.content}
                        onChange={(e) => setNewKnowledge({ ...newKnowledge, content: e.target.value })}
                        className="w-full px-4 py-2.5 rounded-xl border border-gray-200 text-sm placeholder:text-gray-400 resize-none focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all bg-white"
                      />
                    </div>
                  ) : (
                    <div className="flex flex-col items-center justify-center py-8 px-6 rounded-xl border-2 border-dashed border-indigo-200 bg-white">
                      <div className="w-12 h-12 rounded-xl bg-indigo-100 flex items-center justify-center mb-3">
                        <Cloud className="w-6 h-6 text-indigo-500" />
                      </div>
                      <div className="flex items-center gap-1 text-sm mb-1">
                        <Upload className="w-4 h-4 text-indigo-500" />
                        <span className="font-medium text-indigo-600">点击上传</span>
                        <span className="text-gray-500">或拖拽文件</span>
                      </div>
                      <p className="text-xs text-gray-400">支持 PDF, Markdown, TXT 格式</p>
                    </div>
                  )}

                  <div className="flex items-center justify-end gap-3">
                    <button
                      type="button"
                      onClick={() => setShowAddKnowledge(false)}
                      className="px-4 py-2 rounded-lg text-sm font-medium text-gray-600 hover:bg-gray-100 transition-all"
                    >
                      取消
                    </button>
                    <button
                      type="button"
                      onClick={handleAddKnowledge}
                      className="px-4 py-2 rounded-lg bg-indigo-600 text-white text-sm font-medium hover:bg-indigo-700 transition-all"
                    >
                      添加
                    </button>
                  </div>
                </div>
              )}

              {/* Knowledge List */}
              <div className="space-y-3">
                {knowledgeItems.map((item) => (
                  <div
                    key={item.id}
                    className="flex items-center justify-between p-4 rounded-xl border border-gray-100 bg-white hover:border-gray-200 transition-all"
                  >
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 rounded-lg bg-gray-50 flex items-center justify-center">
                        {item.type === "text" ? (
                          <FileText className="w-5 h-5 text-gray-400" />
                        ) : (
                          <Paperclip className="w-5 h-5 text-gray-400" />
                        )}
                      </div>
                      <div>
                        <p className="text-sm font-medium text-foreground">{item.title}</p>
                        <p className="text-xs text-muted-foreground">
                          {item.type === "text" ? "文本知识" : "文件附件"}
                        </p>
                      </div>
                    </div>
                    <div className="flex items-center gap-3">
                      <button
                        type="button"
                        role="switch"
                        aria-checked={item.enabled}
                        onClick={() => toggleKnowledgeEnabled(item.id)}
                        className={cn(
                          "relative w-10 h-5 rounded-full transition-colors duration-200 ease-in-out",
                          item.enabled ? "bg-indigo-600" : "bg-gray-200"
                        )}
                      >
                        <span
                          className={cn(
                            "absolute top-0.5 left-0.5 w-4 h-4 bg-white rounded-full shadow transition-transform duration-200 ease-in-out",
                            item.enabled ? "translate-x-5" : "translate-x-0"
                          )}
                        />
                      </button>
                      <button
                        type="button"
                        onClick={() => handleDeleteKnowledge(item.id)}
                        className="w-8 h-8 rounded-lg flex items-center justify-center text-gray-400 hover:text-red-500 hover:bg-red-50 transition-all"
                      >
                        <Trash2 className="w-4 h-4" />
                      </button>
                    </div>
                  </div>
                ))}

                {knowledgeItems.length === 0 && !showAddKnowledge && (
                  <div className="flex flex-col items-center justify-center py-12 text-center">
                    <div className="w-14 h-14 rounded-2xl bg-gray-50 flex items-center justify-center mb-4">
                      <FileText className="w-7 h-7 text-gray-300" />
                    </div>
                    <p className="text-sm text-muted-foreground mb-4">暂无知识库配置</p>
                    <button
                      type="button"
                      onClick={() => setShowAddKnowledge(true)}
                      className="flex items-center gap-2 px-4 py-2 rounded-lg bg-indigo-600 text-white text-sm font-medium hover:bg-indigo-700 transition-all"
                    >
                      <Plus className="w-4 h-4" />
                      添加知识
                    </button>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>

        {/* Sticky Footer */}
        <div className="px-8 py-4 bg-white/80 backdrop-blur-md border-t border-gray-100 flex items-center justify-between">
          <div className="flex items-center gap-2 text-sm text-gray-400">
            <Clock className="w-4 h-4" />
            <span>最后保存: 刚刚</span>
          </div>
          <div className="flex items-center gap-3">
            <button
              type="button"
              onClick={handleReset}
              className="px-5 py-2.5 rounded-xl border border-gray-200 text-sm font-medium text-gray-600 hover:bg-gray-100 transition-all"
            >
              重置
            </button>
            <button
              type="submit"
              onClick={handleSubmit}
              className="px-6 py-2.5 rounded-xl bg-indigo-600 text-sm font-medium text-white hover:bg-indigo-700 shadow-lg shadow-indigo-500/30 hover:shadow-xl hover:shadow-indigo-500/40 transition-all"
            >
              {editingService ? "保存修改" : "创建服务"}
            </button>
          </div>
        </div>
      </SheetContent>
    </Sheet>
  )
}
